.link {
    list-style: none;
    text-decoration: none;
    font-style: normal;
}

.list {
    list-style: none;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.button {
    cursor: pointer;
}

img {
    display: block;
    width: 100%;
    max-width: 100%;
    height: auto;
}