.heart {
  width: 24px;
  height: 11px;
  display: inline-block;
  position: relative;
  transform: rotate(45deg);
}

.heart div {
  width: 7px;
  height: 7px;
  background: #ff6b01;
  animation: heart 1.2s cubic-bezier(.215, .61, .355, 1) infinite;
  position: relative;
  top: 2px;
  left: 10px;
}

.heart div:after, .heart div:before {
  content: " ";
  width: 7px;
  height: 7px;
  background: #ff6b01;
  display: block;
  position: absolute;
}

.heart div:before {
  border-radius: 50% 0 0 50%;
  left: -7px;
}

.heart div:after {
  border-radius: 50% 50% 0 0;
  top: -7px;
}

@keyframes heart {
  0% {
    transform: scale(.95);
  }

  5% {
    transform: scale(1.4);
  }

  39% {
    transform: scale(1.6);
  }

  45% {
    transform: scale(1.5);
  }

  60% {
    transform: scale(.95);
  }

  100% {
    transform: scale(.9);
  }
}

